body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

a {
  text-decoration: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  border: 0;
}

body {
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
}

.container {
  max-width: 1200px;
  padding-left: 0;
  padding-right: 0;
  margin: 0 auto;
}

.menu {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: space-between;
  align-items: center;
  background-color: #ffffff;
  padding: 28px;
}

.logo {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(16.666666666666668% - 30px);
  width: 131px;
}

.menu__nav {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(58.333333333333336% - 30px);
}

.menu__list {
  display: flex;
  justify-content: space-between;
}

.menu__list .menu__item {
  color: #777777;
  font-family: Roboto;
  font-size: 14px;
  font-weight: 700;
  line-height: 24px;
  text-transform: uppercase;
}

.menu__list .menu__item a {
  text-decoration: none;
  color: #777777;
  padding: 10px;
  transition: 0.5s ease-in;
}

.menu__list .menu__item a:hover {
  color: #ffffff;
  background-color: #000000;
}

.slider .img {
  background-image: url(../img/shutterstock_353200073.png);
  background-size: cover;
  background-position: center top;
  background-repeat: no-repeat;
  width: 100%;
  height: 600px;
}

.slider .img2 {
  background-image: url(../img/slider_01-mokhgqwtr9u2rwxhakqtevd7311vu1meqdvfikirwo.png);
  background-size: cover;
  background-position: center top;
  background-repeat: no-repeat;
  width: 100%;
  height: 600px;
}

.slider .img3 {
  background-image: url(../img/slider3.png);
  background-size: cover;
  background-position: center top;
  background-repeat: no-repeat;
  width: 100%;
  height: 600px;
}

.slider .wrap {
  width: 100%;
  height: 600px;
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.slider .wrap h2 {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(100% - 30px);
  background: #000;
  padding: 10px;
  width: 700px;
  color: #ffffff;
  font-size: 48px;
  font-weight: 300;
  line-height: 24px;
  text-transform: uppercase;
  letter-spacing: 4.8px;
  margin-bottom: 38px;
}

.slider .wrap p {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(100% - 30px);
  color: #000000;
  font-family: Roboto;
  font-size: 18px;
  font-weight: 300;
  line-height: 24px;
  text-transform: uppercase;
  letter-spacing: 7.2px;
}

.slick-prev,
.slick-next {
  font-size: 0;
  line-height: 0;
  position: absolute;
  top: 50%;
  display: block;
  width: 40px;
  height: 40px;
  padding: 0;
  transform: translate(0, -50%);
  cursor: pointer;
  color: transparent;
  border: none;
  background: transparent;
}

.slick-prev {
  left: 50px;
  z-index: 2;
}

.slick-next {
  right: 50px;
}

.slick-prev:before,
.slick-next:before {
  font-family: 'slick';
  font-size: 43px;
  line-height: 1;
  opacity: .75;
  color: black;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-dots {
  position: absolute;
  bottom: 50px;
  display: block;
  width: 100%;
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: center;
}

#slick-slide-control00 {
  border: 5px solid #ffffff;
  border-radius: 50%;
}

.pagination__wrap {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: center;
  align-items: center;
  padding: 24px 0;
}

.pagination__wrap .pagination {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(66.66666666666667% - 30px);
  display: flex;
  justify-content: space-between;
}

.pagination__wrap .pagination a {
  transition: 0.4s;
}

.pagination__wrap .pagination a:hover {
  color: #000;
}

.pagination__wrap .pagination__item {
  color: #777777;
  font-size: 14px;
  font-weight: 700;
  line-height: 24px;
  text-transform: uppercase;
  position: relative;
}

.pagination__wrap .pagination__item::after {
  content: '';
  display: block;
  width: 2px;
  height: 20px;
  position: absolute;
  right: -25px;
  top: 0;
  background-color: #d7d7d7;
  transform: rotate(30deg);
}

.pagination__wrap .pagination .last__item {
  color: #777777;
  font-size: 14px;
  font-weight: 700;
  line-height: 24px;
  text-transform: uppercase;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.gallery .column {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(33.333333333333336% - 30px);
  display: flex;
  flex-direction: column;
}

.gallery .column img {
  margin-bottom: 32px;
}

.xz {
  height: 350px;
  position: relative;
  margin-bottom: 32px;
}

.xz__text {
  position: absolute;
  top: 0;
  width: 100%;
  opacity: 0;
  font-size: 50px;
  font-weight: 300;
  line-height: 350px;
  text-align: center;
  color: #fff;
  transition: 0.5s ease-in;
}

.xz:hover p {
  opacity: 1;
  background: rgba(0, 0, 0, 0.5);
  cursor: pointer;
}

.load {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: center;
}

.load button {
  margin-bottom: 83px;
  padding: 10px 24px;
  color: #ffffff;
  background-color: #959595;
  font-size: 13px;
  font-weight: 700;
  line-height: 20.57px;
  text-transform: uppercase;
  letter-spacing: 0.76px;
  transition: 0.5s ease-in;
}

.load button:hover {
  background-color: #000000;
}

.footer__wrap {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  flex-direction: column;
  align-items: center;
}

.footer__logo {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(100% - 30px);
  width: 131px;
  margin-bottom: 68px;
}

.social {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(50% - 30px);
}

.social__list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.social__list .social__item {
  margin-bottom: 62px;
}

.social__list .social__item a {
  color: #777777;
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
}

.social__list .social__item a:hover {
  color: #000000;
}

.copyring {
  color: #9c9c9c;
  font-family: Roboto;
  font-size: 13px;
  font-weight: 400;
  line-height: 26px;
  text-transform: uppercase;
  letter-spacing: 0.33px;
}

@media screen and (max-width: 1100px) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (max-width: 960px) {
  .container {
    padding-left: undefined;
    padding-right: undefined;
  }
}

@media screen and (max-width: 780px) {
  .container {
    padding-left: undefined;
    padding-right: undefined;
  }
}

@media screen and (max-width: 560px) {
  .container {
    padding-left: undefined;
    padding-right: undefined;
  }
}